
.main-top-nav {
  background-color: #42A5F5;
}
.main-top-nav .ui.menu {
  font-size: 14px;
  border-radius: 0;
  height: 50px;
}
.main-top-nav .ui.menu .ui.dropdown .menu > .header {
  font-size: 1em;
}
.main-top-nav .ui.menu .ui.dropdown .menu > .item {
  font-size: 0.95em !important;
  color: #666 !important;
}
.main-top-nav .ui.menu .item.logo {
  cursor: pointer;
}
.main-top-nav .ui.menu .item {
  padding-left: 1rem;
  padding-right: 1rem;
}
.main-top-nav .ui.menu .item:first-child {
  padding-left: 0;
}
.main-top-nav .ui.menu .item:last-child {
  padding-right: 0;
}
.main-top-nav .ui.menu .item .icon {
  padding: 0 0.5rem;
  margin: 0;
}
.main-top-nav .ui.menu .item .help.icon {
  padding: 0 0.5rem;
  margin-right: 0.5rem;
}
.main-top-nav .ui.menu .item .alarm {
  padding: 0 0.5rem;
  margin-right: 0.5rem;
  font-size: 12px;
}
.main-top-nav .chat-item {
  position: relative;
}
.main-top-nav .chat-item i {
  position: absolute;
  right: -4px;
  top: 0;
  min-width: 16px;
  height: 16px;
  border-radius: 8px;
  background: red;
  color: white;
  font-style: normal;
  font-size: 13px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 3px;
  z-index: 1;
}



