@import "~assets/css/base-variables.less";

@media (min-width: @max-width)
{
  #app {
    .app-content {
      max-width: @max-width;
      margin-left: auto;
      margin-right: auto;
    }
    .main-top-nav, .secondary-nav {
      width: 100%;
      padding: 0 ~"calc(50% - 750px)"
    }
  }
  .toasted-container.bottom-right {
    margin-right: ~"calc(50% - 750px)";
  }
}

@media (max-width: @max-width)
{
  #app {
    width: 100%;
    min-width: @min-width;
    .app-content {
      margin-left: 1.5rem;
      margin-right: 1.5rem;
    }
    .main-top-nav, .secondary-nav{
      padding: 0 1.5rem;
    }
  }
  .toasted-container.bottom-right {
    margin-right: 1.5rem;
  }
}

:root {
 font-size: 14px;
}

body {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  color: #444;
  -webkit-font-smoothing: antialiased;
  margin: 0;
  background-color: #eef0f3;
  min-height: 100vh;
}

* {
  box-sizing: border-box;
}

[v-cloak] {
  display: none;
}

.app-content {
  margin-top: 2rem;
}

input {
  font-size: inherit;
}

.para-text {
  font-family: @para-font-family !important;
}



