@import "~assets/css/base-variables.less";

.ui.form .field>label {
  font-weight: normal;
}

.ui.label.text-danger {
  color: @color-red;
}

.ui.label.text-muted {
  color: #a0abba;
}

.ui.header {
  color: #333;
}

.ui.statistic>.label, .ui.statistics .statistic>.label {
  font-weight: normal;
  color: @color-extra-light-black;
}

.ui.brown.label, .ui.brown.labels .label {
  background-color: @color-brown-300 !important;
}

.ui.selection.list .list>.item.active, .ui.selection.list>.item.active,
.ui.selection.list.list>.item:hover, .ui.selection.list>.item:hover {
  background: none;
  background-color: @color-grey-100;
}

.loading {
  pointer-events: none !important;
}

.ui.secondary.pointing.menu {
  border-bottom-width: 1px;
  .active.item, .active.item:hover, .active.item:active, .active.item:focus {
    color: @color-primary;
    border-color: @color-primary;
  }
  .item {
    color: @color-grey-700;
    margin-bottom: -1px;
    border-bottom-width: 2px;
    padding-left: 0;
    padding-right: 0;
    margin-left: 1rem;
    margin-right: 1rem;
  }
  a.item:active {
    border-color: transparent;
  }
}

.ui.secondary.pointing.menu .item:first-child {
  margin-left: 0;
}
.ui.secondary.pointing.menu .item:last-child {
  margin-right: 0;
}

.ui.checkbox input[type=checkbox], .ui.checkbox input[type=radio] {
  width: 1em;
  height: 1em;
}

.ui.checkbox {
  margin-right: 1em;
  line-height: 1;
  label {
    padding-left: 1.4em;
    cursor: pointer;
  }
  label:before {
    width: 1em;
    height: 1em;
  }
  label:after {
    font-size: 1em !important;
    width: 1em;
    height: 1em;
    left: 1px;
  }
}

.ui.radio.checkbox {
  line-height: 1;
  label {
    padding-left: 1.4em;
    cursor: pointer;
  }
  label:before {
    width: 1em;
    height: 1em;
    top: 0;
  }
  label:after {
    background-color: @color-primary;
    width: 1em;
    height: 1em;
    left: 1px;
    transform: scale(.7);
  }
  input:focus:checked~label:after,
  input:checked~label:after {
    background-color: @color-primary;
  }
}

.ui.checkbox input:checked~label:after,
.ui.checkbox input:checked:focus~label:after, .ui.checkbox input:not([type=radio]):indeterminate:focus~label:after {
  color: white;
}

.ui.checkbox input:checked~label:before,
.ui.checkbox input:checked:focus~label:before, .ui.checkbox input:not([type=radio]):indeterminate:focus~label:before {
  background-color: @color-primary;
  border-color: @color-primary;
}

@toggle-checkbox-height: 1.2em;
.ui.toggle.checkbox {
  min-height: @toggle-checkbox-height;
  vertical-align: top;
  input {
    width: 2.8em;
    height: @toggle-checkbox-height;
  }
  label {
    min-height: @toggle-checkbox-height;
  }
  label:before {
    width: 2.8em;
    height: @toggle-checkbox-height;
  }
  label:after {
    height: @toggle-checkbox-height;
    width: @toggle-checkbox-height;
  }
  input:checked~label:after {
    left: 1.6em;
  }
  .box:after, label:after {
    box-shadow: 0 1px 2px 0 rgba(34,36,38,.15), 0 0 0 1px rgba(34,36,38,.15) inset !important;
  }
}



